<template>
  <div>
    <div>
      <Modal
        v-model="modal" width="900">
        <p slot="header">
          <span></span>
        </p>
        <div>
          <Form ref="siteuser" :model="form" :label-width="90" :rules="AddRule" class="node-add-form">
            <Form-item label="" prop="detail">
              <Input type="text" v-model="form.name" placeholder=""></Input>
            </Form-item>
            <Form-item label="" prop="account">
              <Input type="text" v-model="form.account" placeholder=""></Input>
            </Form-item>
            <Form-item label="" prop="email">
              <Input type="text" v-model="form.email" placeholder=""></Input>
            </Form-item>
            <Form-item label="" prop="pwd">
              <Input type="password" v-model="form.pwd" placeholder=""></Input>
            </Form-item>
            <Form-item label="" prop="confirmPwd">
              <Input type="password" v-model="form.confirmPwd" placeholder=""></Input>
            </Form-item>
          </Form>
        </div>
        <div slot="footer">
          <Button type="success" size="large" :loading="modal_loading" @click="add"></Button>
        </div>
      </Modal>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js';
  export default {
    data() {
      return {
        editorOption: {},
        modal: false,
        modal_loading: false,
        form: {
          name: "",
          account:"",
          pwd: "",
          email:'',
          confirmPwd:''
        },
        AddRule: {
          name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          account: [
            {required: true, message: '', trigger: 'blur'},
          ],
          pwd: [
            {required: true, message: '', trigger: 'blur'},
          ],
          email: [
            {required:true,message:'',trigger:'blur'}
          ],
          confirmPwd: [
            {required:true,message:'',trigger:'blur'}
          ]
        }
      }
    },
    methods: {
      computed: {
        editor() {
          return this.$refs.myTextEditor.quillEditor
        }
      },
        add() {
          this.$refs.siteuser.validate((valid) => {
              if(valid){
                if(this.form.pwd !== this.form.confirmPwd){
                  this.$Message.error('');
                  return
                }
                this.modal_loading = true;
                let data = this.form;
                this.apiPost('siteuser', data).then((res) => {
                  this.handelResponse(res, (data, msg) => {
                    this.modal = false;
                    this.$parent.getData();
                    this.$Message.success(msg);
                    this.modal_loading = false;
                    this.$refs.siteuser.resetFields();
                  }, (data, msg) => {
                    this.modal_loading = false;
                    this.$Message.error(msg);
                  })
                }, (res) => {
                  //
                  this.modal_loading = false;
                  this.$Message.error('');
                })
              }
          })
        }
    },
    mixins: [http]
  }
</script>



