<template>
  <div>
    <div style="margin: 10px">
      <Button type="success" shape="circle" icon="plus" @click="add"></Button>
    </div>
    <Table :context="self" :border="border" :stripe="stripe" :show-header="showheader"
           :size="size" :data="nodelist" :columns="tableColumns3" style="width: 100%">
    </Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="total" :current="current" @on-change="changePage" show-total
              show-elevator></Page>
      </div>
    </div>
    <!---->
    <Nodeadd ref="add" :company="companylist" :user="userlist"></Nodeadd>
    <Nodeedit ref="edit" :form="editinfo" :company="companylist" :user="userlist"></Nodeedit>
  </div>
</template>
<script>
  import http from '../../../assets/js/http.js';
  import Nodeadd from './nodeadd.vue';
  import Nodeedit from './nodeedit.vue';
  export default {
    data () {
      return {
        self: this,
        border: true,
        stripe: true,
        showheader: true,
        size: 'small',
        total: 0,
        current: 1,
        pagesize: 10,
        name: '',
        nodelist: [],
        editinfo: {},
        userlist: [],
        companylist: [],
      }
    },
    components: {Nodeadd, Nodeedit},
    created () {
      this.getData();
      this.getCompany();
      this.getUser();
    },
    methods: {
      getUser(){
        this.apiGet('user/getAll').then((res) => {
          this.handelResponse(res, (data, msg) => {
//            console.log(data)
            this.userlist = data;
          }, (data, msg) => {
            this.$Message.error(msg);
          })
        }, (res) => {
          //
          this.$Message.error('');
        });
      },
      getCompany(){
        this.apiGet('company/getAll').then((res) => {
          this.handelResponse(res, (data, msg) => {
            this.companylist = data;
          }, (data, msg) => {
            this.$Message.error(msg);
          })
        }, (res) => {
          //
          this.$Message.error('');
        });
      },
      //
      getData(){
        let data = {
          params: {
            'page': this.current,
            'rows': this.pagesize,
            /*            'name': this.name,
             'industry_id': this.industry_id*/
          }
        };
        this.apiGet('node', data).then((data) => {
          this.handelResponse(data, (data, msg) => {
//            console.log(data)
            this.nodelist = data.rows
            this.total = data.total;
          }, (data, msg) => {
            this.$Message.error(msg);
          })
        }, (data) => {
          this.$Message.error('');
        })
      },
      queryData(){
        this.getData();
      },
      changePage(page){
        this.current = page;
        this.getData();
      },
      changePageSize(pagesize){
        this.pagesize = pagesize
        this.getData()
      },
      add(){
        this.$refs.add.modal = true
      },
      edit(index){
        //
        //
        let editid = this.nodelist[index].id
        this.apiGet('node/' + editid).then((res) => {
          this.handelResponse(res, (data, msg) => {
            delete  data.create_time;
            delete  data.update_time;
            this.editinfo = data
            this.modal = false;
            this.$refs.edit.modal = true
          }, (data, msg) => {
            this.$Message.error(msg);
          })
        }, (res) => {
          //
          this.$Message.error('');
        })
      },
      on (index) {
        //
        let id = this.nodelist[index].id
        let _this = this
        let data = {
          params: {
            id: id,
            status: 'on'
          }
        };
        this.$Modal.confirm({
          title: '',
          content: '?',
          okText: '',
          cancelText: '',
          onOk: (index) => {
            _this.apiGet('node/status/', data).then((res) => {
              _this.handelResponse(res, (data, msg) => {
                _this.getData()
                _this.$Message.success(msg);
              }, (data, msg) => {
                _this.$Message.error(msg);
              })
            }, (res) => {
              //
              _this.$Message.error('');
            })
          },
          onCancel: () => {
            return false
          }
        })
      },
      off(index){
        let id = this.nodelist[index].id
        let _this = this
        let data = {
          params: {
            id: id,
            status: 'off'
          }
        };
        this.$Modal.confirm({
          title: '',
          content: '?',
          okText: '',
          cancelText: '',
          onOk: (index) => {
            _this.apiGet('node/status/', data).then((res) => {
              _this.handelResponse(res, (data, msg) => {
                _this.getData()
                _this.$Message.success(msg);
              }, (data, msg) => {
                _this.$Message.error(msg);
              })
            }, (res) => {
              //
              _this.$Message.error('');
            })
          },
          onCancel: () => {
            return false
          }
        })
      }
    },
    computed: {
      tableColumns3()
      {
        let columns = [
          {
            type: 'index', width: 60, align: 'center'
          },
          {
            title: '', key: 'name', sortable: true
          },
          {
            title: '', key: 'detail', sortable: true
          },
          {
            title: '', key: 'com_name', sortable: true
          },
          {
            title: '', key: 'user_name'
          },
          {
            title: '', key: 'create_time',
          }
        ];
        columns.push(
          {
            title: '',
            key: 'status',
            align: 'center',
            render (row, column, index) {
              if (row.status == 'on') {
                return '<span style="font-weight: bold;" title=""><Icon type="ios-checkmark-empty" size="30"></Icon></span>';
              } else {
                return '<span style="color: red;font-weight: bold;" title=""><Icon type="ios-close-empty" size="30" ></Icon> </span>'
              }
            }
          }),
          columns.push(
            {
              title: '',
              key: 'action',
              width: 150,
              align: 'center',
              fixed: 'right',
              render (row, column, index) {
                let btn = ''
                if (row.status == 'off') {
                  btn = `<i-button type="error" size="small" @click="on(${index})"></i-button>`;
                } else {
                  btn = `<i-button type="error" size="small" @click="off(${index})"></i-button>`;
                }
                return `<i-button type="primary" size="small" @click="edit(${index})"></i-button> ` + btn;

              }
            }
          );
        return columns;
      }
    },
    mixins: [http]
  }
</script>



