// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import iView from 'iview';
import 'iview/dist/styles/iview.css';    //  CSS
import '../my-theme/index.less'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import Drilldown from '../node_modules/highcharts/modules/drilldown.js'
import Highcharts from 'highcharts'
Drilldown(Highcharts);

import axios from 'axios'
import Lockr from 'lockr'
import moment from 'moment'
import Cookies from 'js-cookie'
import VueQuillEditor from 'vue-quill-editor'
import VueHtml5Editor from 'vue-html5-editor'

Vue.config.productionTip = false
Vue.use(iView);
Vue.use(router);
Vue.use(ElementUI)
Vue.use(VueQuillEditor)
Vue.use(VueHtml5Editor, {
  // new VueHtml5Editor(options)
  // global component name
  name: "editor",
  // 
  // if set true,will append module name to toolbar after icon
  showModuleName: false,
  // classfont-awesome
  // custom icon class of built-in modules,default using font-awesome
  icons: {
    text: "custom-icon text",
    color: "custom-icon color",
    font: "custom-icon font",
    align: "custom-icon align",
    list: "custom-icon list",
    link: "custom-icon link",
    unlink: "custom-icon unlink",
    tabulation: "custom-icon table",
    image: "custom-icon image",
    "horizontal-rule": "custom-icon hr",
    eraser: "custom-icon eraser",
    hr: "custom-icon hr",
    undo: "custom-icon undo",
    "full-screen": "custom-icon full-screen",
    info: "custom-icon info ",
  },
  // 
  // config image module
  image: {
    //   max file size
    sizeLimit: 512 * 1024,
    // ,base64
    // upload config,default null and convert image to base64
    upload: {
      url: null,
      headers: {},
      params: {},
      fieldName: {}
    },
    // ,localResizeIMG,null
    // compression config,default resize image by localResizeIMG (https://github.com/think2011/localResizeIMG)
    // set null to disable compression
    compress: {
      width: 1600,
      height: 1600,
      quality: 80
    },
    // ,
    // handle response datareturn image url
    uploadHandler(responseText){
      //default accept json data like  {ok:false,msg:"unexpected"} or {ok:true,data:"image url"}
      var json = JSON.parse(responseText)
      if (!json.ok) {
        alert(json.msg)
      } else {
        return json.data
      }
    }
  },
  // en-uszh-cn
  //default en-us, en-us and zh-cn are built-in
  language: "zh-cn",
  // 
  i18n: {
    //specify your language here
    "zh-cn": {
      "align": "",
      "image": "",
      "list": "",
      "link": "",
      "unlink": "",
      "table": "",
      "font": "",
      "full screen": "",
      "text": "",
      "eraser": "",
      "color": "",
      "please enter a url": "",
      "create link": "",
      "bold": "",
      "italic": "",
      "underline": "",
      "strike through": "",
      "subscript": "",
      "superscript": "",
      "heading": "",
      "font name": "",
      "font size": "",
      "left justify": "",
      "center justify": "",
      "right justify": "",
      "ordered list": "",
      "unordered list": "",
      "fore color": "",
      "background color": "",
      "row count": "",
      "column count": "",
      "save": "",
      "upload": "",
      "progress": "",
      "unknown": "",
      "please wait": "",
      "error": "",
      "abort": "",
      "reset": ""
    }
  },
  // 
  // the modules you don't want
  hiddenModules: [],
  // 
  // keep only the modules you want and customize the order.
  // can be used with hiddenModules together
  visibleModules: [
    "text",
    "color",
    "font",
    "align",
    "list",
    "link",
    "unlink",
    "tabulation",
    "image",
    "hr",
    "eraser",
    "undo",
    "full-screen",
  ],
  // examples
  // extended modules
  modules: {
    //omit,reference to source code of build-in modules
  }
})

let domain = 'api.salesman.cc'

// let domain = 'www.sitegroupback.com'

var HOST = 'http://' + domain + '/index.php/'
var ROOTHOST = 'http://' + domain + '/'

axios.defaults.baseURL = HOST
axios.defaults.timeout = 1000 * 15
axios.defaults.headers['Content-Type'] = 'application/json'
axios.defaults.withCredentials = true

//
window.HOST = HOST
window.ROOTHOST = ROOTHOST
window.router = router
window.axios = axios
window.moment = moment
window.Cookies = Cookies
window.Lockr = Lockr

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: {App}
})



