<!--  tree -->
<template>
  <div>
    <Row>
      <Col span="24" style="padding: 10px;">
      <Button type="primary" shape="circle" icon="android-add" @click="addkeyword"></Button>
      <Button type="success" shape="circle" icon="android-add" @click="addAkeyword">A</Button>
      <Button type="success" shape="circle" icon="android-add" @click="addBkeyword">B/C</Button>
      <Button type="success" shape="circle" icon="android-done" @click="saveKeyword"></Button>
      <Button type="error" shape="circle" icon="android-add" @click="removeKeyword"></Button>
      </Col>
    </Row>
    <el-input
      placeholder=""
      v-model="filterText"
      style="margin-bottom: 10px;">
    </el-input>
    <el-tree :data="data"
             :props="props"
             :load="loadNode"
             class="filter-tree"
             lazy="" show-checkbox
             check-strictly
             @check-change="change"
             ref="tree"
             :filter-node-method="filterNode">
    </el-tree>
    <!---->
    <Alert type="warning" style="margin-top: 10px">
     <span style="margin-top:20px;font-size:16px;font-weight: bold"></span>
       <p style="font-size:14px;line-height: 23px">A,&nbsp;&nbsp;&nbsp;AB,&nbsp;&nbsp;&nbsp;,&nbsp;&nbsp;&nbsp;BC</p>
       <p style="font-size:14px;">B,C,B,C</p>
    </Alert>
    <keywordUpload ref="upload" :id="checkedNodeId"></keywordUpload>
    <AkeywordAdd ref="akeywordadd"></AkeywordAdd>
    <BkeywordAdd ref="bkeywordadd" :pid="checkedNodeId"></BkeywordAdd>
    <CkeywordAdd ref="ckeywordadd"></CkeywordAdd>
    <Updatekeyword ref="updatekeyword" :datas="form"></Updatekeyword>
  </div>
</template>


<script>
  import http from '../../../assets/js/http.js';
  import keywordUpload from './keywordupload.vue';
  import AkeywordAdd from './Akeywordadd.vue';
  import BkeywordAdd from './Bkeywordadd.vue';
  import CkeywordAdd from './Ckeywordadd.vue';
  import Updatekeyword from './Updatekeyword.vue';

  export default {
    watch: {
      filterText(val) {
        this.$refs.tree.filter(val);
      }
    },
    components: {keywordUpload, AkeywordAdd,BkeywordAdd,CkeywordAdd,Updatekeyword},
    data() {
      return {
        checkedNodeId: 0,
        data: [],
        props: {
          label: 'label',
          children: 'children'
        },
        filterText: '',
        ids:[],
        form:{}
      };
    },
    methods: {
      saveKeyword() {
        let node = this.$refs.tree.getCheckedNodes();
        if (node.length !=1) {
          this.$Message.info('');
          return
        }
        this.form=node[0]
        this.$refs.updatekeyword.modal=true;
      },
      change(data,current,sel) {

      },
      addkeyword(){
        let node = this.$refs.tree.getCheckedNodes();
        if (node.length == 1) {
          if (node[0].tag == 'C') {
            this.$Message.info('C');
            return;
          }
          this.checkedNodeId = node[0].id;
          this.$refs.upload.modal = true;
        } else if (node.length == 0) {
          this.$Message.info('');
        } else {
          this.$Message.info('');
        }
      },
      removeKeyword(){
        let node = this.$refs.tree.getCheckedNodes();
        node.forEach((item) => {
          this.ids.push(item.id)
        })

          let _this = this
          let data = {
            id:this.ids,
          };
          this.$Modal.confirm({
            title: '',
            content: '?',
            okText: '',
            cancelText: '',
            onOk: () => {
              _this.apiPost('admin/deleAll',data).then((res) => {
                _this.handelResponse(res, (data, msg) => {
                  setTimeout(function () {
                    location.reload();
                  }, 1000);
                  // 
                  _this.$Message.success(msg);
                }, (data, msg) => {
                  _this.$Message.error(msg);
                })
              }, (res) => {
                //
                _this.$Message.error('');
              })
            },
            onCancel: () => {
              return false
            }
          })

      },
      //A 
      addAkeyword(){
        //A  
        this.$refs.akeywordadd.modal = true
      },
      addBkeyword(){
        let node = this.$refs.tree.getCheckedNodes();
        if (node.length == 1) {
          if (node[0].tag == 'C') {
            this.$Message.info('C');
            return;
          }
          this.checkedNodeId = node[0].id;
          this.$refs.bkeywordadd.modal = true
        } else if (node.length == 0) {
          this.$Message.info('');
        } else {
          this.$Message.info('');
        }
      },
      //
      filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      //
      getData(id, func){
        let data = {}
        if (id != 0) {
          data = {
            params: {
              id: id,
            }
          }
        }
        this.apiGet('keyword', data).then((res) => {
          this.handelResponse(res, (childdata, msg) => {
            func(childdata)
          }, (data, msg) => {
            this.$Message.error(msg);
          })
        }, (res) => {
          //
          this.$Message.error('');
        })
      },
      loadNode(node, resolve) {
        if (node.level === 0) {
          //
          this.getData(0, (data) => {
            return resolve(data);
          });
        } else if (node.level >= 3) {
          return resolve([]);
        } else {
          this.getData(node.data.id, (data) => {
            resolve(data)
          });
        }
      }
    },
    mixins: [http]
  };
</script>



