<template>
  <div>
    <Modal
      v-model="modal" width="900">
      <p slot="header">
        <span></span>
      </p>
      <div>
        <Upload
          type="drag"
          with-credentials
          name="file_name"
          ref="upload"
          :format="['zip']"
          :on-success="getResponse"
          :on-error="getErrorInfo"
          :on-format-error="formatError"
          :action="action">
          <div style="padding: 20px 0">
            <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
            <p></p>
          </div>
        </Upload>
        <Form ref="save" :model="form" :label-width="90" :rules="SaveRule" class="node-add-form">
          <Form-item label="">
            <a v-bind:href="downloadPath" target="_blank"></a>
          </Form-item>
          <Form-item label="">
            <a v-bind:href="viewPath" target="_blank"></a>
          </Form-item>
          <Form-item label="" prop="title">
            <Input type="text" v-model="form.name" placeholder="/"></Input>
          </Form-item>
          <Form-item label="/" prop="come_from">
            <Input type="text" v-model="form.detail" placeholder="/" style="width: 200px;"></Input>
          </Form-item>
          <Form-item label="" prop="directory_name">
            <Input type="text" v-model="form.directory_name" placeholder=""></Input>
          </Form-item>
        </Form>
      </div>
      <div slot="footer">
        <Button type="success" size="large" :loading="modal_loading" @click="activitysave"></Button>
      </div>
    </Modal>
  </div>
</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js';
  export default {
    data() {
      return {
        modal: false,
        modal_loading: false,
        action: HOST + 'activity/uploadActivity',
        SaveRule: {
          name: [
            {required: true, message: '/', trigger: 'blur'},
          ],
          detail: [
            {required: true, message: '/', trigger: 'blur'},
          ],
          directory_name:[
            {required:true,message:'',trigger:'blur'}
          ]
        }
      }
    },
    computed: {
      downloadPath: function () {
        return ROOTHOST + this.form.code_path;
      },
      viewPath: function () {
        return ROOTHOST + this.form.demo_path;
      }
    },
    methods: {
      getResponse(response, file, filelist){
        this.form.code_path = response.data.code_path;
        this.$Message.success(response.msg);
      },
      getErrorInfo(error, file, filelist){
        this.$Message.error(error);
      },
      formatError(){
        this.$Message.error(' zip');
      },
      activitysave() {
        this.$refs.save.validate((valid) => {
          if (valid) {
            this.modal_loading = true;
            let data = this.form;
            let id = data.id;
            this.apiPut('activity/' + id, data).then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.$parent.getData();
                this.$Message.success(msg);
                this.modal_loading = false;
                this.$refs.save.resetFields();
                this.$refs.upload.clearFiles();
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.$Message.error('');
            })
          }
        })
      }
    },
    mixins: [http],
    props: {
      form: {
        default: {
          name: "",
          detail: '',
          code_path: '',
          demo_path: ''
        }
      }
    }
  }
</script>



