<template>
  <div>
    <Modal
      v-model="modal" width="900">
      <p slot="header">
        <span></span>
      </p>
      <div>
        <Form ref="save" :model="form" :label-width="90" :rules="AddRule" class="node-add-form">
          <Form-item label="" prop="title">
            <Input type="text" v-model="form.title" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="summary">
            <Input type="text" v-model="form.summary" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="source">
            <Input type="text" v-model="form.source" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="content">
            <editor @change="updateData" :content="form.content"  :height="300" :auto-height="false"></editor>
          </Form-item>
        </Form>
      </div>

      <div slot="footer">
        <Button type="success" size="large" :loading="modal_loading" @click="save"></Button>
      </div>
    </Modal>
  </div>

</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js';
  export default {
    data() {
      return {
        editorOption: {},
        modal: false,
        modal_loading: false,
        AddRule: {
          title: [
            {required: true, message: '', trigger: 'blur'},
          ],
          summary: [
            {required: true, message: '', trigger: 'blur'},
          ],
          source:[
            {required:true,message:'',trigger:'blur'}
          ]
        }
      }
    },
    methods: {
      updateData(data) {
        this.form.content = data
      },
//      changeArticletype(value) {
//        this.form.articletype_name = value.label
//        this.form.articletype_id = value.value
//      },
      save() {
        this.$refs.save.validate((valid) => {
          if (valid) {
            this.modal_loading = true;
            let data = this.form;
            let id = data.id;
            this.apiPost('sys/changeWecatArticle', data).then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.$parent.getData();
                this.$Message.success(msg);
                this.modal_loading = false;
//                this.$refs.save.resetFields();
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.$Message.error('');
            })
          }
        })
      }
    },
    mixins: [http],
    props: {

      form: {
        default: {
          title: "",
          content: ''
        }
      }
    }
  }
</script>
<style>
  .ql-container .ql-editor {
    min-height: 20em;
    padding-bottom: 1em;
    max-height: 25em;
  }
</style>



