<template>
  <Modal v-model="modal" title="ftp">
    <Form ref="ftp" :model="form"  :label-width="90" :rules="AddRule" class="node-add-form">
      <Form-item label="ftp" prop="ftp_place">
        <Input type="text" v-model="form.ftp_place"  placeholder="ftp"></Input>
      </Form-item>
      <Form-item label="ftp host" prop="ftp_host">
        <Input type="text" v-model="form.ftp_host" placeholder="ftp host"></Input>
      </Form-item>
      <Form-item label="ftp " prop="ftp_user">
        <Input type="text" v-model="form.ftp_user"  placeholder="ftp"></Input>
      </Form-item>
      <Form-item label="ftp " prop="ftp_pwd">
        <Input type="password" v-model="form.ftp_pwd"  placeholder="ftp"></Input>
      </Form-item>
    </Form>
    <div slot="footer">
      <Button type="success" size="large" :loading="modal_loading" @click="submitFtp"></Button>
    </div>
  </Modal>

</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js';
  export default{
    data() {
      return {
        modal: false,
        modal_loading:false,
        AddRule: {
          ftp_place: [
            {required: true, message: 'ftp', trigger: 'blur'},
          ],
          ftp_host: [
            {required: true, message: 'ftp host', trigger: 'blur'},
          ],
          ftp_user: [
            {required: true, message: 'ftp', trigger: 'blur'},
          ],
          ftp_pwd: [
            {required: true, message: 'ftp', trigger: 'blur'},
          ]
        }
      }
    },
    methods: {
      submitFtp(){
        this.$refs.ftp.validate((valid) => {
          if (valid) {
            let data = this.form;
            let id = this.ftp_id;
            this.apiPut('Site/saveFtp/'+ id, data).then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal_loading = false;
                this.modal = false;
                this.$parent.getData();
                this.$Message.success(msg);
              }, (data, msg) => {
                this.$Message.error(msg);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.modal = false
              this.$Message.error('');
            })
          }
        })
      }
    },
    props: {
      form: {
        default: {

        }
      },
      ftp_id:Number
    },
    mixins: [http]
  }
</script>
<style>


</style>



