<template>
  <div>
    <div class="top">
      :
      <Input v-model="title" placeholder="" style="width:300px;"></Input>
      :
      <Select v-model="article_type" style="width: 250px;" label-in-value filterable clearable>
        <Option disabled value="">
          
        </Option>
        <Option v-for="item in articletypelist" :value="item.id" :label="item.name" :key="item">
          {{ item.text }}
        </Option>
      </Select>
      <Button type="primary" @click="queryData"></Button>
      <Button type="success" @click="add"></Button>
    </div>
    <div class="content" style="margin-top:10px;">
      <Table :context="self" :border="border" :stripe="stripe" :show-header="showheader"
             :size="size" :data="datas" :columns="tableColumns" style="width: 100%">
      </Table>
      <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
          <Page v-show="page_show" :total="total" :current="current" :page-size="pageSize" @on-change="changePage"
                @on-page-size-change="changePageSize"
                show-total
                show-elevator show-sizer></Page>
        </div>
      </div>
    </div>
    <articleadd ref="add" :articletype="articletypelist"></articleadd>
    <articlesave ref="save" :form="editinfo" :articletype="articletypelist"></articlesave>
    <articleshow ref="show" :form="editinfo"></articleshow>
  </div>

</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js'
  import common from '../../../assets/js/common.js'
  import articleadd from './add.vue'
  import articlesave from './save.vue'
  import articleshow from './show.vue'

  export default {
    data () {
      return {
        page_show: true,
        self: this,
        border: true,
        stripe: true,
        current: 1,
        showheader: true,
        showIndex: true,
        size: 'small',
        total: 0,
        page: 1,
        rows: 10,
        pageSize: 10,
        title: '',
        article_type: 0,
        datas: [],
        editinfo: {},
        articletypelist: []
      }
    },
    components: {articleadd, articlesave, articleshow},
    created () {
      this.getData();
      this.getArticleType((data) => {
        this.articletypelist = data
      });
    },
    methods: {
      getData() {
        let data = {
          params: {
            page: this.page,
            rows: this.rows,
            title: this.title,
            article_type: this.article_type
          }
        }
        this.apiGet('article', data).then((data) => {
          this.handelResponse(data, (data, msg) => {
            this.datas = data.rows
            this.total = data.total;
            this.pageSize = 10
          }, (data, msg) => {
            this.$Message.error(msg);
          })
        }, (data) => {
          this.$Message.error('');
        })
      },
      changePage(page){
        this.page = page;
        this.getData();
      },
      changePageSize(pagesize){
        this.rows = pagesize;
        this.getData();
      },
      queryData(){
        this.page = 1
        this.page_show = false
        this.getData();
        this.page_show = true
      },
      add(){
        this.$refs.add.modal = true
      },
      edit(index){
        this.getArticle(index);
        this.$refs.save.modal = true
      },
      show(index){
        this.getArticle(index);
        this.$refs.show.modal = true
      },
      getArticle(index){
        let editid = this.datas[index].id
        this.apiGet('article/' + editid).then((res) => {
          this.handelResponse(res, (data, msg) => {
            this.editinfo = data
          }, (data, msg) => {
            this.$Message.error(msg);
          })
        }, (res) => {
          //
          this.$Message.error('');
        })
      },
      remove(index){
        //
        let id = this.datas[index].id
        let _this = this
        this.$Modal.confirm({
          title: '',
          content: '?',
          okText: '',
          cancelText: '',
          onOk: (index) => {
            _this.apiDelete('article/', id).then((res) => {
              _this.handelResponse(res, (data, msg) => {
                _this.getData()
                _this.$Message.success(msg);
              }, (data, msg) => {
                _this.$Message.error(msg);
              })
            }, (res) => {
              //
              _this.$Message.error('');
            })
          },
          onCancel: () => {
            return false
          }
        })
      },
      changeSync(index, is_sync){
        //
        let id = this.datas[index].id
        let _this = this
        let data = {
          'is_sync': is_sync,
          id: id
        }
        if (data.is_sync == 10) {
          this.$Modal.confirm({
            title: '',
            content: '?',
            okText: '',
            cancelText: '',
            onOk: (index) => {
              _this.apiPost('article/sync', data).then((res) => {
                _this.handelResponse(res, (data, msg) => {
                  _this.getData();
                  _this.$Message.success(msg);
                }, (data, msg) => {
                  _this.$Message.error(msg);
                })
              }, (res) => {
                //
                _this.$Message.error('');
              })
            },
            onCancel: () => {
              return false
            }
          })
        } else if (data.is_sync == 20) {
          this.$Modal.confirm({
            title: '',
            content: '?',
            okText: '',
            cancelText: '',
            onOk: (index) => {
              _this.apiPost('article/sync', data).then((res) => {
                _this.handelResponse(res, (data, msg) => {
                  _this.getData();
                  _this.$Message.success(msg);
                }, (data, msg) => {
                  _this.$Message.error(msg);
                })
              }, (res) => {
                //
                _this.$Message.error('');
              })
            },
            onCancel: () => {
              return false
            }
          })
        }


      },
    },
    computed: {
      tableColumns()
      {
        let columns = [];
        if (this.showCheckbox) {
          columns.push({
            type: 'selection',
            width: 60,
            align: 'center'
          })
        }
        columns.push({
            title: 'ID',
            key: 'id',
            width: 60,
            align: 'center'
          })
        columns.push({
          title: '',
          key: 'title',
          sortable: true
        });
        columns.push({
          title: '',
          key: 'articletype_name',
          sortable: true
        });
        columns.push({
          title: '',
          key: 'auther',
          sortable: true
        });
        columns.push({
          title: '',
          key: 'create_time',
          sortable: true
        });
        columns.push(
          {
            title: '',
            key: 'action',
            align: 'center',
            fixed: 'right',
            render (row, column, index) {
              var btn = '';
              if (row.site_id != '0') {
                var btn = `<i-button type="primary" size="small" title="" @click="changeSync(${index},'10')"></i-button>`;
                if (row.is_sync == '10') {
                  //20  
                  btn = `<i-button type="error" size="small" title="" @click="changeSync(${index},'20')"></i-button>`;
                }
              }
              return `<i-button type="success" size="small" @click="edit(${index})"></i-button>
<i-button type="info" size="small" @click="show(${index})"></i-button>

                      <!--<i-button type="error" size="small" @click="remove(${index})"></i-button>&nbsp;-->


` + btn;
            }
          }
        );
        return columns;
      }
    },
    mixins: [http, common]
  }

</script>
<style>


</style>



