<style>
  .user-edit-form {
    padding: 20px;
  }
</style>
<template>
  <div>
    <Modal
      v-model="modal">
      <p slot="header">
        <Icon type="information-circled"></Icon>
        <span></span>
      </p>
      <div>
        <Form ref="useredit" :model="form" :label-width="90" :rules="userEditRule" class="user-edit-form">
          <Form-item label="" prop="user_name">
            <Input type="text" v-model="form.user_name" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="pwd">
            <Input type="password" v-model="form.pwd" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="pwd2">
            <Input type="password" v-model="form.pwd2" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="name">
            <Input type="text" v-model="form.name" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="contacts">
            <Input type="text" v-model="form.contacts" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="mobile">
            <Input type="text" v-model="form.mobile" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="tel">
            <Input type="text" v-model="form.tel" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="email">
            <Input type="text" v-model="form.email" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="wechat">
            <Input type="text" v-model="form.wechat" placeholder=""></Input>
          </Form-item>
          <Alert type="error"></Alert>
          <Alert type="error"></Alert>
        </Form>
      </div>
      <div slot="footer">
        <Button type="success" size="large" :loading="modal_loading" @click="edit"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import http from '../../../assets/js/http.js'
  export default {
    data () {
      return {
        modal: false,
        modal_loading: false,
        userEditRule: {
          user_name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          pwd: [
            {required: false, message: '', trigger: 'blur'},
            {type: 'string', min: 6, message: '6', trigger: 'blur'}
          ],
          pwd2: [
            {required: false, message: '', trigger: 'blur'},
            {type: 'string', min: 6, message: '6', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          contacts: [
            {required: true, message: '', trigger: 'blur'},
          ],
          mobile: [
            {required: true, message: '', trigger: 'blur'},
          ],
        },
      }
    },
    methods: {
      edit()
      {
        if (this.form.pwd != '' || this.form.pwd2 != '') {
          //  
          this.userEditRule.pwd2[0].required = true;
          this.userEditRule.pwd[0].required = true;
        }
        this.$refs.useredit.validate((valid) => {
          if (valid) {
            if (this.form.pwd != this.form.pwd2) {
              this.$Message.info('');
              return false
            }
            this.modal_loading = true;
            let data = this.form;
            let id = data.id;
//            console.log(id)
            delete data['pwd2'];
            delete data['create_time'];
            data['type_name'] = this.form.type == 1 ? '' : '';
            this.apiPut('user/' + id, data).then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.$parent.getData();
                this.$Message.success(msg);
                this.modal_loading = false;
                this.$refs.useredit.resetFields();
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg);
              })
            }, (res) => {
              //
              this.$Message.error('');
              this.modal_loading = false;
            })
          } else {
            return false;
          }
        })
        //   
        this.userEditRule.pwd2[0].required = false;
        this.userEditRule.pwd[0].required = false;
      }
    },
    props: {
      form: {
        default: {
          user_name: '',
          pwd: '',
          pwd2: '',
          type: 2,
          name: '',
          mobile: '',
          tel: '',
          email: '',
          wechat: ''
        }
      }
    },
    mixins: [http]
  }
</script>



