<template>
  <Modal v-model="modal2" width="460">
    <p slot="header" style="color:dodgerblue;text-align:center;font-size: 18px;">
      <Icon type="flag"></Icon>
      <span></span>
    </p>
    <div style="text-align:center">
      <Button type="info" @click="aKeyGeneration"></Button>
      <Button type="warning" @click="generatArticle"></Button>
      <Button type="error" @click="generatMenu"></Button>
      <Button type="success" @click="generatIndex"></Button>
    </div>
    <div slot="footer">
    </div>
  </Modal>
</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js';
  export default {
    data() {
      return {
        modal2: false,
        modal_loading: false,
      }
    },
    methods: {
      aKeyGeneration() {
        this.$Modal.confirm({
          title: '',
          content: '?',
          okText: '',
          cancelText: '',
          onOk: (index) => {
            let id = this.genaraterId;
            this.apiGet('Site/siteGetCurl/' + id + '/aKeyGeneration').then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.modal_loading = false;
                this.$Message.success(msg);
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg, 5);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.$Message.error('');
            })
          },
          onCancel: () => {
            return false
          }
        })
      },
      generatIndex() {
        this.$Modal.confirm({
          title: '',
          content: '?',
          okText: '',
          cancelText: '',
          onOk: (index) => {
            let id = this.genaraterId;
            this.apiGet('Site/siteGetCurl/' + id + '/generatIndex').then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.modal_loading = false;
                this.$Message.success(msg);
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg, 5);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.$Message.error('');
            })
          },
          onCancel: () => {
            return false
          }
        })
      },
      generatArticle() {
        this.$Modal.confirm({
          title: '',
          content: '?',
          okText: '',
          cancelText: '',
          onOk: (index) => {
            let id = this.genaraterId;
            this.apiGet('Site/siteGetCurl/' + id + '/generatArticle').then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.modal_loading = false;
                this.$Message.success(msg);
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg, 5);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.$Message.error('');
            })
          },
          onCancel: () => {
            return false
          }
        })
      },
      generatMenu() {
        this.$Modal.confirm({
          title: '',
          content: '?',
          okText: '',
          cancelText: '',
          onOk: (index) => {
            let id = this.genaraterId;
            this.apiGet('Site/siteGetCurl/' + id + '/generatMenu').then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.modal_loading = false;
                this.$Message.success(msg);
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg, 5);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.$Message.error('');
            })
          },
          onCancel: () => {
            return false
          }
        })
      }
    },
    props: {
      genaraterId: Number
    },
    mixins: [http]
  }

</script>
