<template>
  <div>
    <div>
      <Modal
        v-model="modal" width="900">
        <p slot="header">
          <span></span>
        </p>
        <div>
          <Form ref="sitesave" :model="form" :label-width="90" :rules="AddRule" class="node-add-form">
            <Form-item label="" prop="site_name">
              <Input type="text" v-model="form.site_name" placeholder=""></Input>
            </Form-item>
            <Form-item label="" prop="com_name">
              <Input type="text" v-model="form.com_name" placeholder=","></Input>
            </Form-item>
            <Form-item label="" prop="user_id">
              <Select v-model="form.user_id" style="text-align: left;width:200px;"
                      label-in-value @on-change="changeUser">
                <Option v-for="item in userlist" :value="item.id" :label="item.text" :key="item">
                  {{ item.text }}
                </Option>
              </Select>
            </Form-item>
            <Form-item label="" prop="is_mobile">
              <Radio-group v-model="form.is_mobile">
                <Radio label=10>
                  <Icon type="social-windows"></Icon>
                  <span>PC</span>
                </Radio>
                <Radio label=20>
                  <Icon type="android-phone-portrait"></Icon>
                  <span></span>
                </Radio>
              </Radio-group>
            </Form-item>
            <Form-item label="" prop="m_site_id">
              <Select v-model="form.m_site_id"  clearable style="text-align: left;width:200px;"
                      label-in-value >
                <Option v-for="item in mobileSite" :value="item.id" :label="item.text" :key="item">
                  {{ item.text }}
                </Option>
              </Select>
            </Form-item>
            <Form-item label="" prop="keyword_ids">
              <Select v-model="form.keyword_ids" multiple style="text-align: left;width:200px;" @on-change="changekeyword">
                <Option v-for="item in keyword" :value="item.id" :label="item.label" :key="item">
                  {{ item.label }}
                </Option>
              </Select>
            </Form-item>
            <Form-item label="" prop="menu">
              <Select filterable v-model="form.menu" multiple style="text-align: left;width:350px;">
                <Option disabled :value="0"><span style="font-size: 15px;font-weight: bold"></span></Option>
                <Option v-for="item in menutype" :value="item.id" :label="item.text" :key="item">
                  {{ item.text}}{{item.tag_name}}{{item.flag_name}}
                  <span v-if="item.type_name ==''">{{item.type_name}}</span>
                  <span v-else>{{item.typeName}}</span>{{item.title}}
                </Option>

              </Select>
            </Form-item>
            <Form-item label="" prop="template_id">
              <Select v-model="form.template_id" style="text-align: left;width:200px;"
                      label-in-value @on-change="changeTemptype">
                <Option v-for="item in temptype" :value="item.id" :label="item.text" :key="item">
                  {{ item.text }}
                </Option>
              </Select>
            </Form-item>
            <Form-item label="" prop="support_hotline">
              <Select v-model="form.support_hotline" style="text-align: left;width:200px;"
                      label-in-value @on-change="changeHotline">
                <Option v-for="item in hotline" :value="item.id" :label="item.text" :key="item">
                  {{ item.text }}
                </Option>
              </Select>
            </Form-item>
            <Form-item label="" prop="site_type">
              <Select v-model="form.site_type" style="text-align: left;width:200px;"
                      label-in-value @on-change="changeSitetype">
                <Option v-for="item in sitetype" :value="item.id" :label="item.text" :key="item">
                  {{ item.text }}
                </Option>
              </Select>
            </Form-item>
            <Form-item label="" prop="domain_id">
              <Select v-model="form.domain_id" style="text-align: left;width:200px;"
                      label-in-value @on-change="changeDomainlist">
                <Option v-for="item in domainlist" :value="item.id" :label="item.text" :key="item">
                  {{ item.text }}
                </Option>
              </Select>
            </Form-item>
            <Form-item label="" prop="link_id">
              <Select v-model="form.link_id" multiple  style="text-align: left;width:200px;">
                <Option v-for="item in link" :value="item.id" :label="item.text" :key="item">
                  {{ item.text }}
                </Option>
              </Select>
            </Form-item>
            <Form-item label="url" prop="url">
              <Input v-model="form.url" placeholder="url"></Input>
            </Form-item>
            <Form-item label="" prop="public_code">
              <Select v-model="form.public_code" multiple style="text-align: left;width:200px;">
                <Option v-for="item in code" :value="item.id" :label="item.text" :key="item">
                  {{ item.text }}
                </Option>
              </Select>
            </Form-item>
            <Form-item label="" prop="walterString">
              <Input type="text" v-model="form.walterString" placeholder=""></Input>
            </Form-item>
            <Form-item label="head" prop="before_header_jscode">
              <Input v-model="form.before_header_jscode" type="textarea" :rows="3"
                     placeholder="head">
              </Input>
            </Form-item>
            <Form-item label="head" prop="other_jscode">
              <Input v-model="form.other_jscode" type="textarea" :rows="3"
                     placeholder="head">
              </Input>
            </Form-item>
          </Form>
        </div>
        <div slot="footer">
          <Button type="success" size="large" :loading="modal_loading" @click="add"></Button>
        </div>
      </Modal>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js';
  export default {
    data() {
      const checkmenutype = (rule, value, callback) => {
        if (value=="") {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      const checkkeyword = (rule, value, callback) => {
        if (value=="") {
          callback(new Error(''));
        } else if(value.length>5){
          callback(new Error('5'));
        }else {
          callback();
        }

      };
      const checktemptype = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      const checkhotlinetype = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      const checksitetype = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''));
        } else {
          callback();

        }
      };
      const checkdomain = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''));
        } else {
          callback();
        }
      }
      const checkuser = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''));
        } else {
          callback();
        }
      };

      return {
        modal: false,
        modal_loading: false,
        AddRule: {
          site_name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          com_name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          menu: [
            {required: true,validator: checkmenutype, trigger: 'blur'},
          ],
          keyword_ids: [
            {required: true,validator: checkkeyword, trigger: 'blur'},
          ],
          template_id: [
            {required: true,validator: checktemptype, trigger: 'blur'},
          ],
          support_hotline: [
            {required: true,validator: checkhotlinetype, trigger: 'blur'},
          ],
          site_type: [
            {required: true,validator: checksitetype, trigger: 'blur'},
          ],
          domain_id: [
            {required: true,validator: checkdomain, trigger: 'blur'},
          ],
          user_id: [
            {required: true,validator: checkuser, trigger: 'blur'},
          ],
          url:[
            {required: true, message: 'url', trigger: 'blue'}
          ]

        }
      }
    },
    methods: {
      changeMenutype(value) {
          this.form.menu = value.label
      },
      changeHotline(value) {
        this.form.support_hotline = value.value
      },
      changeSitetype(value) {
        this.form.site_type = value.value
        this.form.site_type_name = value.label
      },
      changeUser(value){
        this.form.user_name = value.label
        this.form.user_id = value.value
      },
      changekeyword(){

      },

      changeTemptype(value) {
        this.form.template_id = value.value
      },
      changeDomainlist(value) {
        this.form.domain = value.label
        this.form.domain_id = value.value
      },
        add() {
          this.$refs.sitesave.validate((valid) => {
              if(valid){
                this.modal_loading = true;
                if(!this.form.walterString){
                  this.form.walterString=this.form.site_name
                }
                let data = this.form;
                let id = data.id;
                this.apiPut('site/'+ id, data).then((res) => {
                  this.handelResponse(res, (data, msg) => {
                    this.modal = false;
                    this.$parent.getData();
                    this.$Message.success(msg);
                    this.modal_loading = false;
                  }, (data, msg) => {
                    this.modal_loading = false;
                    this.$Message.error(msg);
                  })
                }, (res) => {
                  //
                  this.modal_loading = false;
                  this.$Message.error('');
                })
              }
          })
        }
    },
    props: {
      code: {
        default:[]
      },
      menutype: {
        default:
          []
      }
      ,
      temptype: {
        default:
          []
      }
      ,
      sitetype: {
        default:
          []
      }
      ,
      hotline: {
        default:
          []
      }
      ,
      domainlist:{
        default:
          []
      },
      userlist:{
        default:
          []
      },
      keyword:{
        default:
          []
      },
      mobileSite:{

      },
      link:{
        default:
          []
      },
      form: {
        default: {
          site_name: "",
          com_name: "",
          menu: "",
          link_id: "",
          template_id:"",
          support_hotline:"",
          site_type:"",
          domain_id:"",
          before_header_jscode:"",
          other_jscode:"",
          keyword_ids:"",
        }
      }
    },
    mixins: [http]
  }
</script>



