<template>
  <div>
    <div>
      <Modal
        v-model="modal" width="600">
        <p slot="header">
          <span></span>
        </p>
        <div>
          <Form ref="titlesave" :model="form" :label-width="90" :rules="SaveRule">
            <Form-item label="" prop="title">
              <Input type="text" v-model="form.title" placeholder=""></Input>
            </Form-item>
            <Form-item label="" prop="articletype_id">
              <Select ref="select"    :clearable="selects" v-model="form.articletype_id" style="text-align: left;width:250px;"
                      label-in-value @on-change="changeArticletype">
                <Option v-for="item in articletype" :value="item.id" :label="item.name" :key="item">
                  {{ item.text }}
                </Option>
              </Select>
            </Form-item>
          </Form>
        </div>
        <Alert type="warning">
           <b></b>  <b></b>  15,   
        </Alert>
        <div slot="footer">
          <Button type="success" size="large" :loading="modal_loading" @click="save"></Button>
        </div>
      </Modal>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js';
  export default {
    data() {
      const checkarticetype = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      return {
        modal: false,
        selects:true,
        modal_loading: false,
        SaveRule: {
          title: [
            {required: true, message: '', trigger: 'blur'},
          ],
          articletype_id: [
            {validator: checkarticetype, trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      changeArticletype(type) {
        this.form.articletype_name = type.label;
      },
      clearTiTleType(){
          console.log(this.$refs.select.clearSingleSelect());
      },
      save() {
        this.$refs.titlesave.validate((valid) => {
          if (valid) {
            this.modal_loading = true;
            let data = this.form
            let id = data.id;
            this.apiPut('scatteredTitle/' + id, data).then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.$parent.getData();
                this.$Message.success(msg);
                this.modal_loading = false;
                this.$refs.select.clearSingleSelect();
                this.$refs.titlesave.resetFields();
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.$Message.error('');
            })
          }
        })
      }
    },
    props: {
      form: {
        default: {
          title: '',
          articletype_id: 0,
          articletype_name: ''
        }
      },
      articletype: {
        default: []
      }
    },
    mixins: [http]
  }
</script>



