<style>
  .user-add-form {
    padding: 20px;
  }
</style>
<template>
  <div>
    <Modal
      v-model="modal"
      ok-text="" width="600">
      <p slot="header">
        <Icon type="person-add"></Icon>
        <span></span>
      </p>
      <div>
        <Form ref="useradd" :model="form" :label-width="90" :rules="userAddRule" class="user-add-form">
          <Form-item label="" prop="user_name">
            <Input type="text" v-model="form.user_name" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="pwd">
            <Input type="password" v-model="form.pwd" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="pwd2">
            <Input type="password" v-model="form.pwd2" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="type">
            <Select v-model="form.type" style="width:150px;text-align: left">
              <Option v-for="item in type" :value="item.value" :key="item">{{ item.label }}</Option>
            </Select>
          </Form-item>
          <Form-item label="" prop="name">
            <Input type="text" v-model="form.name" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="contacts">
            <Input type="text" v-model="form.contacts" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="mobile">
            <Input type="text" v-model="form.mobile" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="tel">
            <Input type="text" v-model="form.tel" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="email">
            <Input type="text" v-model="form.email" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="wechat">
            <Input type="text" v-model="form.wechat" placeholder=""></Input>
          </Form-item>
        </Form>
      </div>
      <div slot="footer">
        <Button type="success" size="large" :loading="modal_loading" @click="add"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import http from '../../../assets/js/http.js'
  export default {
    data () {
      return {
        modal: false,
        modal_loading: false,
        form: {
          user_name: '',
          pwd: '',
          pwd2: '',
          type: 2,
          name: '',
          mobile: '',
          tel: '',
          email: '',
          wechat: ''
        },
        userAddRule: {
          user_name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          pwd: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'string', min: 6, message: '6', trigger: 'blur'}
          ],
          pwd2: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'string', min: 6, message: '6', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          contacts: [
            {required: true, message: '', trigger: 'blur'},
          ],
          mobile: [
            {required: true, message: '', trigger: 'blur'},
          ],
        },
        type: [
          {
            value: 1,
            label: ''
          }, {
            value: 2,
            label: ''
          },
        ]
      }
    },
    methods: {
      add()
      {
        this.$refs.useradd.validate((valid) => {
          if (valid) {
            if (this.form.pwd != this.form.pwd2) {
              this.$Message.info('');
              return false
            }
            this.modal_loading = true;
            let data = this.form;
            delete data['pwd2'];
            data['type_name'] = this.form.type == 1 ? '' : '';
            this.apiPost('user', data).then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.$parent.getData();
                this.$Message.success(msg);
                this.modal_loading = false;
                this.$refs.useradd.resetFields();
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.$Message.error('');
            })
          } else {
            return false;
          }
        })
      }
      ,
    }
    ,
    mixins: [http]
  }
</script>



