<template>
  <div>
    <div style="margin: 10px">
      
      <Radio-group v-model="usertype" type="button">
        <Radio label="all"></Radio>
        <Radio label="1"></Radio>
        <Radio label="2"></Radio>
      </Radio-group>
      
      <Input v-model="name" placeholder="" style="width: 300px"></Input>
      <Button type="primary" icon="ios-search" @click="queryData"></Button>
      <Button type="success" shape="circle" icon="plus" @click="add"></Button>
    </div>
    <Table :context="self" :border="border" :stripe="stripe" :show-header="showheader"
           :size="size" :data="userlist" :columns="tableColumns3" style="width: 100%">
    </Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="total" :current="current" @on-change="changePage" @on-page-size-change="changePageSize" show-total
              show-elevator ></Page>
      </div>
    </div>
    <!---->
    <Useradd ref="add"></Useradd>
    <Useredit ref="edit" :form="editinfo"></Useredit>
  </div>
</template>
<script>
  import http from '../../../assets/js/http.js';
  import Useradd from './useradd.vue';
  import Useredit from './useredit.vue';
  export default {
    data () {
      return {
        self: this,
        border: true,
        stripe: true,
        showheader: true,
        size: 'small',
        total: 0,
        current: 1,
        pagesize: 10,
        name: '',
        userlist: [],
        usertype: 'all',
        editinfo: {},
      }
    },
    components: {Useradd, Useredit},
    created () {
      this.getData();
    },
    methods: {
      //
      getData(){
        let usertype = '';
        if (this.usertype != 'all') {
          usertype = this.usertype;
        }
        let data = {
          params: {
            'page': this.current,
            'rows': this.pagesize,
            'type': usertype,
            'name': this.name
          }
        };
        this.apiGet('User', data).then((data) => {
          this.handelResponse(data, (data, msg) => {
            this.userlist = data.rows
            this.total = data.total;
          }, (data, msg) => {
            this.$Message.error(msg);
          })
        }, (data) => {
          this.$Message.error('');
        })
      },
      queryData(){
        this.getData();
      },
      changePage(page){
        this.current = page;
        this.getData();
      },
      changePageSize(pagesize){
        this.pagesize = pagesize
        this.getData()
      },
      add(){
        this.$refs.add.modal = true
      },
      edit(index){
        //
        //
        let editid = this.userlist[index].id
        this.apiGet('user/' + editid).then((res) => {
          this.handelResponse(res, (data, msg) => {
            data.pwd = '';
            data.pwd2 = '';
            delete  data.create_time;
            delete  data.update_time;
            this.editinfo = data
            this.modal = false;
            this.$refs.edit.modal = true
          }, (data, msg) => {
            this.$Message.error(msg);
          })
        }, (res) => {
          //
          this.$Message.error('');
        })
      },
      remove (index) {
        //
        let id = this.userlist[index].id
        let _this = this
        this.$Modal.confirm({
          title: '',
          content: '?',
          okText: '',
          cancelText: '',
          onOk: (index) => {
            _this.apiDelete('user/', id).then((res) => {
              _this.handelResponse(res, (data, msg) => {
                _this.getData()
                _this.$Message.success(msg);
              }, (data, msg) => {
                _this.$Message.error(msg);
              })
            }, (res) => {
              //
              _this.$Message.error('');
            })
          },
          onCancel: () => {
            return false
          }
        })
      },
    }
    ,
    computed: {
      tableColumns3()
      {
        let columns = [];
        if (this.showCheckbox) {
          columns.push({
            type: 'selection',
            width: 60,
            align: 'center'
          })
        }
        if (this.showIndex) {
          columns.push({
            type: 'index',
            width: 60,
            align: 'center'
          })
        }
        columns.push({
          title: '',
          key: 'user_name',
          sortable: true
        });
        columns.push({
          title: '',
          key: 'name'
        });
        columns.push({
          title: '',
          key: 'type_name',
          sortable: true,
          filters: [
            {
              label: '',
              value: 1
            },
            {
              label: '',
              value: 2
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            if (value === 1) {
              return row.type == 1;
            } else if (value === 2) {
              return row.type == 2;
            }
          }
        });
        columns.push(
          {
            title: '',
            key: 'contacts',
          }
        );
        columns.push(
          {
            title: '',
            key: 'mobile',
          }
        );
        columns.push(
          {
            title: '',
            key: 'tel',
          }
        );
        columns.push(
          {
            title: '',
            key: 'wechat',
          }
        );
        columns.push(
          {
            title: '',
            key: 'email',
          }
        );
        columns.push({
          title: '',
          key: 'create_time'
        });
        columns.push(
          {
            title: '',
            key: 'action',
            width: 150,
            align: 'center',
            fixed: 'right',
            render (row, column, index) {
              return `<i-button type="primary" size="small" @click="edit(${index})"></i-button>   <i-button type="error" size="small" @click="remove(${index})"></i-button>`;
            }
          }
        );
        return columns;
      }
    }
    ,
    mixins: [http]
  }
</script>



