<template>
  <div>
    <div>
      <Modal
        v-model="modal" width="600">
        <p slot="header">
          <span></span>
        </p>
        <div>
          <Upload
            type="drag"
            ref="uploadzip"
            with-credentials
            name="file_name"
            :format="['zip']"
            :on-success="getResponse"
            :on-error="getErrorInfo"
            :on-format-error="formatError"
            :action="action">
            <div style="padding: 20px 0">
              <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
              <p></p>
            </div>
          </Upload>
          <Form ref="templatesave" :model="form" :label-width="90" :rules="AddRule" class="node-add-form">
            <Form-item label="">
              <a v-bind:href="downloadPath" target="_blank"></a>
            </Form-item>
            <Form-item label="" prop="name">
              <Input type="text" v-model="form.name" placeholder=""></Input>
            </Form-item>
            <Form-item label="" prop="detail">
              <Input type="text" v-model="form.detail" placeholder=""></Input>
            </Form-item>
          </Form>
        </div>
        <div slot="footer">
          <Button type="success" size="large" :loading="modal_loading" @click="add"></Button>
        </div>
      </Modal>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js';
  export default {
    data() {
      return {
        modal: false,
        modal_loading: false,
        action: HOST + 'template/uploadtemplate',
        AddRule: {
          name: [
            {required: true, message: '', trigger: 'blur'},
          ],
        }
      }
    },
    computed: {
      downloadPath: function () {
        return ROOTHOST + this.form.path;
      }
    },
    methods: {
      getResponse(response, file, filelist){
        this.form.path = response.data;
        this.$Message.success(response.msg);
      },
      getErrorInfo(error, file, filelist){
        this.$Message.error(error);
      },
      formatError(){
        this.$Message.error(' zip');
      },
      add() {
        this.$refs.templatesave.validate((valid) => {
          if (valid) {
            this.modal_loading = true;
            let data = this.form;
            let id = data.id;
            this.apiPut('template/' + id, data).then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.$parent.getData();
                this.$Message.success(msg);
                this.modal_loading = false;
                this.$refs.templatesave.resetFields();
                this.$refs.uploadzip.clearFiles()
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.$Message.error('');
            })
          }
        })
      }
    },
    props: {
      form: {
        default: {
          name: '',
          detail: '',
          path: ''
        }
      }
    },
    mixins: [http]
  }
</script>



