<template>
  <div>
    <div>
      <Modal
        v-model="modal" width="600">
        <p slot="header">
          <span></span>
        </p>
        <div>
          <Form ref="domainsave" :model="form" :label-width="90" :rules="AddRule">
            <Form-item label="" prop="domain">
              <Input type="text" v-model="form.domain" placeholder=""></Input>
            </Form-item>
            <Form-item label=""prop="domain_info" >
              <Input type="text" v-model="form.domain_info" placeholder="" ></Input>
            </Form-item>
            <Form-item label="" >
              <Input type="text" v-model="form.registrant_user" placeholder="" ></Input>
            </Form-item>
            <Form-item label="" >
              <Input type="text" v-model="form.registrant_tel" placeholder="" ></Input>
            </Form-item>
            <Form-item label="" >
              <Input type="text" v-model="form.registrant_email" placeholder="" ></Input>
            </Form-item>
            <Form-item label="" >
              <Input type="text" v-model="form.filing_num" placeholder="" ></Input>
            </Form-item>
            <Form-item label="" >
              <Input type="text" v-model="form.filing_info" placeholder="" ></Input>
            </Form-item>
            <Form-item label="" >
              <Input type="text" v-model="form.filing_office" placeholder=""></Input>
            </Form-item>
          </Form>

        </div>
        <div slot="footer">
          <Button type="success" size="large" :loading="modal_loading" @click="add"></Button>
        </div>
      </Modal>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js';
  export default {
    data() {
      return {
        modal: false,
        modal_loading: false,
        AddRule: {
          domain: [
            {required: true, message: '', trigger: 'blur'},
          ],
//          registrant_user: [
//            {required: true, message: '', trigger: 'blur'},
//          ],
//          registrant_tel: [
//            {required: true, message: '', trigger: 'blur'},
//          ],
//          registrant_email: [
//            {required: true, message: '', trigger: 'blur'},
//          ],
//          filing_num: [
//            {required: true, message: '', trigger: 'blur'},
//          ]
        }
      }
    },
    methods: {
        add() {
          this.$refs.domainsave.validate((valid) => {
              if(valid){
                this.modal_loading = true;
                let data = this.form;
                let id = data.id;
                this.apiPut('domain/'+ id, data).then((res) => {
                  this.handelResponse(res, (data, msg) => {
                    this.modal = false;
                    this.$parent.getData();
                    this.$Message.success(msg);
                    this.modal_loading = false;
                    this.$refs.domainsave.resetFields();
                  }, (data, msg) => {
                    this.modal_loading = false;
                    this.$Message.error(msg);
                  })
                }, (res) => {
                  //
                  this.modal_loading = false;
                  this.$Message.error('');
                })
              }
          })
        }
    },
    props: {
      form: {
        default: {
          domain: '',
          domain_info:'',
          filing_info:'',
          registrant_user: '',
          registrant_tel: '',
          filing_office:'',
          registrant_email: '',
          filing_num:''
        }
      }
    },
    mixins: [http]
  }
</script>



