<template>
  <div>
    <Modal
      v-model="modal" width="250">
      <p slot="header">
        <span></span>
      </p>
      <div>
        <Form ref="title" :model="form" :label-width="60" :rules="AddRule" class="node-add-form">
          <Form-item label="" prop="sort">
            <Input-number  :min="0" v-model="form.sort"  placeholder=""></Input-number>
          </Form-item>
        </Form>
        <Alert type="error"></Alert>
      </div>
      <div slot="footer">
        <Button type="success" size="large" :loading="modal_loading" @click="savetitle"></Button>
      </div>
    </Modal>
  </div>
</template>

<script type="text/ecmascript-6">
  import http from '../../../assets/js/http.js';
  export default {
    data() {
      const checksort= (rule, value, callback) => {
        if(!/^-?[1-9]\d*$/.test(value)){
          callback(new Error(''));
        } else {
          callback();
        }
      };
      return {
        modal: false,
        modal_loading: false,
        AddRule: {
          sort: [
            {required: true,validator: checksort, trigger: 'blur'}
          ],
        }
      }
    },
    methods: {
        savetitle() {
        this.$refs.title.validate((valid) => {
          if (valid) {
            this.modal_loading = true;
            let data = this.form;
            let id = data.id;
            this.apiPut('menu/' + id, data).then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.modal = false;
                this.$parent.getData();
                this.$Message.success(msg);
                this.modal_loading = false;
                this.$refs.title.resetFields();
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg);
              })
            }, (res) => {
              //
              this.modal_loading = false;
              this.$Message.error('');
            })
          }
        })
      }
    },
    mixins: [http],
    props: {
      form: {
        default: {
          sort: '',
        }
      }
    }
  }
</script>




