<style>
  .change-pwd-form {
    padding: 20px;
  }
</style>
<template>
  <div>
    <Modal
      v-model="modal"
      title=""
      ok-text="">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span></span>
      </p>
      <div style="text-align:center">
        <Form ref="changepwdform" :model="form" :label-width="90" :rules="changePwdRule" class="change-pwd-form">
          <Form-item label="" prop="prepwd">
            <Input type="password" v-model="form.prepwd" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="pwd1">
            <Input type="password" v-model="form.pwd1" placeholder=""></Input>
          </Form-item>
          <Form-item label="" prop="pwd2">
            <Input type="password" v-model="form.pwd2" placeholder=""></Input>
          </Form-item>
        </Form>
      </div>
      <div slot="footer">
        <Button type="success" size="large" :loading="modal_loading" @click="changePwd"></Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import http from '../../assets/js/http.js'
  export default {
    data () {
      return {
        modal: false,
        modal_loading: false,
        form: {
          prepwd: '',
          pwd1: '',
          pwd2: ''
        },
        changePwdRule: {
          prepwd: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'string', min: 6, message: '6', trigger: 'blur'}
          ],
          pwd1: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'string', min: 6, message: '6', trigger: 'blur'}
          ],
          pwd2: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'string', min: 6, message: '6', trigger: 'blur'}
          ]
        },
      }
    },
    methods: {
      changePwd () {
        this.$refs.changepwdform.validate((valid) => {
          if (valid) {
            let data = {}
            data.oldPwd = this.form.prepwd
            if (this.form.pwd1 != this.form.pwd2) {
              this.$Message.info('');
              return false
            }
            this.modal_loading = true;
            data.newPwd = this.form.pwd1
            this.apiPost('common/common/changePwd', data).then((res) => {
              this.handelResponse(res, (data, msg) => {
                this.$Message.success(msg);
                this.modal = false;
              }, (data, msg) => {
                this.modal_loading = false;
                this.$Message.error(msg);
              })
            }, (res) => {
              //
              this.$Message.error('');
            })
          } else {
            return false;
          }
        })
      },
    },
    mixins: [http]
  }
</script>



